form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 30px;
  background-color: snow;
  height: 200px;
  width: 300px;
  border-radius: 10px;
  text-align: center;
  border: 2px solid black;
}

